import React, { Component } from 'react'
import NavComponent from '../nav'
import styles from './index.module.less'
import { RouteView } from '@components/index'
import PreferenceComponent from '../preference'
import NavbarComponent from '../navbar'
import { getUserInfo } from '@apis/users'
import store from '@stores/store'
import { SET_USET_ID } from '@stores/app/app.types'

type IMainLayoutProps = {
  route: any
}

type IMainLayoutState = {
  isLoading: boolean
}

export default class MainLayout extends Component<IMainLayoutProps, IMainLayoutState> {
  constructor(props) {
    super(props)
    this.state = {
      isLoading: true,
    }
  }
  async componentDidMount() {
    try {
      const info = await getUserInfo()
      // @ts-ignore
      const { _id = '' } = info
      store.dispatch({
        type: SET_USET_ID,
        id: _id,
      })
      this.setState({
        isLoading:false
      })
    } catch (e) {
      // @ts-ignore
      this.props.history.push('/login')
    }
  }

  componentDidCatch(error, info) {
    // 捕获子组件渲染错误，可以存储在数据库
    console.log(error, info)
  }

  render() {
    const { route } = this.props
    const { isLoading } = this.state
    return (
      <>
        {isLoading ? (
          <div className={styles.loading}>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        ) : (
          <div className={styles['main-layout']}>
            <div className={styles['main-layout-side']}>
              <img
                className="logo"
                alt="logo"
                src="https://arbeit.oss-cn-beijing.aliyuncs.com/logo.png"
                width="50"
              />
              <div className="navs">
                <NavComponent />
              </div>
              <PreferenceComponent />
            </div>
            <div className={styles['main-layout-main']}>
              <NavbarComponent />
              <div className={styles['main-layout-main-container']}>
                <RouteView routers={route.children} />
              </div>
            </div>
          </div>
        )}
      </>
    )
  }
}
