import { asyncComponent } from '@components/index'
import MainLayout from '@layouts/main'

export const ROUTE_APP_KEY = 'app'

const AboutView = asyncComponent(() => import('@views/about'))
const LoginView = asyncComponent(() => import('@views/login'))
const RegisterView = asyncComponent(() => import('@views/register'))
const NotFontVIew = asyncComponent(() => import('@views/no-fond'))
const TomatoView = asyncComponent(() => import('@views/tomato'))
const UserView = asyncComponent(() => import('@views/user'))
const ProjectView = asyncComponent(() => import('@views/project'))
const ProjectDetailView = asyncComponent(() => import('@views/project/ProjectDetail'))

// 动态路由，根据后台返回的权限动态生成
export const asyncRouters = [
  {
    path: '/tasks',
    component: TomatoView,
    meta: {
      key: 'Tomato',
      name: 'router.tomato',
      className: 'cloud cloud-tomato',
    },
  },
  {
    path: '/projects/:id',
    component: ProjectDetailView,
    meta: {
      key: 'ProjectDetail',
      name: 'router.project',
      isHidden: true,
    },
  },
  {
    path: '/projects',
    component: ProjectView,
    meta: {
      key: 'Project',
      name: 'router.project',
      className: 'cloud cloud-project',
    },
  },
  {
    path: '/users',
    component: UserView,
    meta: {
      key: 'User',
      name: 'router.user',
      className: 'cloud cloud-organizer',
    },
  },
]

// 固定路由
const routes = [
  {
    path: '/login',
    meta: {
      key: 'Login',
      name: 'login',
      isHidden: true,
    },
    component: LoginView,
  },
  {
    path: '/register',
    meta: {
      key: 'Register',
      name: 'register',
      isHidden: true,
    },
    component: RegisterView,
  },
  {
    path: '/about',
    meta: {
      key: 'About',
      name: 'about',
      isHidden: true,
    },
    component: AboutView,
  },
  {
    path: '/',
    component: MainLayout,
    meta: {
      key: ROUTE_APP_KEY,
      name: 'App',
      isHidden: true,
    },
    children: asyncRouters,
  },
  {
    path: '/404',
    meta: {
      key: 'NotFond',
      name: 'notFond',
      isHidden: true,
    },
    component: NotFontVIew,
  },
]

export default routes
